@import '../Layout/index.scss';

.projects-page {
  color: $primary;
  font-family: 'La Belle Aurore';
  overflow-y: scroll;
  height: 100vh;
  gap: 4em;
  left: 74px;

  .text-animate-hover:hover {
    color: $light;
    cursor: none;
  }

  h1 {
    text-align: center;
    font-size: 6em;
    margin: 0;
    margin-bottom: .5em;
  }

  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
    opacity: 0;
    animation: fadeIn 1s 1.5s forwards;


    .project {
      width: 80%;
      opacity: 0;
      animation: fadeIn 2s forwards;

      &:nth-of-type(1) {
        animation-delay: 1.7s;
      }

      &:nth-of-type(2) {
        animation-delay: 2.2s;
      }

      &:nth-of-type(3) {
        animation-delay: 2.7s;
      }

      &:nth-of-type(4) {
        animation-delay: 3.2s;
      }

      &:nth-of-type(5) {
        animation-delay: 3.7s;
      }

      &:nth-of-type(6) {
        animation-delay: 4.2s;
      }

      &:nth-of-type(7) {
        animation-delay: 4.7s;
      }

      &:nth-of-type(8) {
        animation-delay: 5.2s;
      }

      .projectImage {
        width: 100%;
        object-fit: contain;
        border-radius: 6px;
        animation: slideInRight 2s;

        &:nth-of-type(1) {
          animation-delay: 1.6s;
        }

        &:nth-of-type(2) {
          animation-delay: 2.1s;
        }

        &:nth-of-type(3) {
          animation-delay: 2.6s;
        }

        &:nth-of-type(4) {
          animation-delay: 3.1s;
        }

        &:nth-of-type(5) {
          animation-delay: 3.6s;
        }

        &:nth-of-type(6) {
          animation-delay: 4.1s;
        }

        &:nth-of-type(7) {
          animation-delay: 4.6s;
        }

        &:nth-of-type(8) {
          animation-delay: 5.1s;
        }
      }

      .hover-carousel {
        background-color: rgba(4, 36, 53, 0.658);
        position: relative;
        display: grid;
        place-items: center;
        transition: all ease-in-out 1s;

        .projectImage {
          position: relative;
          z-index: -1;
          padding-bottom: -2em;
        }

        h1 {
          position: absolute;
          color: $primary;
          font-size: 4vw;
          text-align: center;
          animation: pulse;
          animation-duration: 1s;

          small {
            font-family: 'Coolvetica';
            font-size: 3vw;
          }

          &.fadeOut {
            animation: fadeOut;
            animation-duration: 1s;
          }
        }
      }
    }
  }
}
