$primary: #ffd700;
$light: #fff;
$dark: #181818;
$secondary: #4d4d4e;

.nav-bar {
  background-color: $dark;
  width: 14rem;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px;

  .logo {
    display: block;
    padding: 8px 0;

    img {
      display: block;
      margin: 8px auto;

      &.sub-logo {
        margin-top: -1em;
      }
    }

  }
}

nav {
  display: block;
  text-align: center;
  position: absolute;
  height: 210px;
  top: 48%;
  margin-top: -100px;
  width: 100%;

  :nth-child(2) {
    margin-block: 1em;
  }

  :nth-child(3) {
    margin-bottom: 1em;
  }

  a {
    font-size: 22px;
    color: $secondary;
    display: block;
    line-height: 51px;
    height: 51px;
    position: relative;
    text-decoration: none;

    i {
      transition: all 0.3s ease-out;
    }

    &:hover {
      color: $primary;

      svg {
        opacity: 0;
      }

      &:after {
        opacity: 1;
      }
    }

    &:after {
      content: "";
      font-size: 14px;
      letter-spacing: 2px;
      position: absolute;
      bottom: 0;
      display: block;
      width: 100%;
      text-align: center;
      opacity: 0;
      transition: all 0.3s ease-out;
      font-weight: 800;
    }

    &:first-child {
      &::after {
        content: 'HOME';
      }
    }

  }

  .about-link {
    &:after {
      content: 'ABOUT';
    }
  }

  .contact-link {
    &:after {
      content: 'CONTACT';
    }
  }

  .projects-link {
    &:after {
      content: 'PROJECTS';
    }
  }

  a.active {
    svg {
      color: $primary;
    }
  }
}

ul {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: block;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 2em 0;

  li {
    a {
      padding: 7px 0;
      display: block;
      font-size: 15px;
      line-height: 16px;

      &:hover svg {
        color: $primary;
      }
    }
  }
}

.responsiveNav {
  display: none;
}
