@import '../Sidebar/index.scss';
$secondary: #8d8d8d;

.home-page {
  display: flex;

  .text-zone {
    font-family: "Coolvetica";
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 40%;
    max-height: 90%;
    z-index: 1000;
  }

  h1 {
    color: $light;
    margin: 0;
    font-size: 53px;
    font-weight: 400;

    &::before {
      content: "<h1>";
      font-family: 'La Belle Aurore';
      color: $primary;
      font-size: 18px;
      position: absolute;
      margin-top: -30px;
      left: 15px;
      opacity: 0.6;
    }

    &::after {
      content: "</h1>";
      font-family: 'La Belle Aurore';
      color: $primary;
      font-size: 18px;
      position: absolute;
      margin-top: 18px;
      opacity: 0.6;
      margin-left: 1em;
      animation: fadeIn 1s 1.7s backwards;
    }

    img {
      margin-left: 20px;
      opacity: 0;
      margin-bottom: -.2em;
      height: auto;
      animation: rotateIn 1s linear both;
      animation-delay: 1.4s;
    }

  }
}

h2 {
  color: $secondary;
  font-weight: 500;
  margin-top: 20px;
  font-size: 14px;
  width: 80vw;
  font-family: sans-serif;
  letter-spacing: 3px;
  animation: fadeIn 1s 1.8s backwards;
}

.flat-button {
  color: $primary;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4px;
  font-family: sans-serif;
  text-decoration: none;
  padding: 10px 18px;
  border: 1px solid $primary;
  margin-top: 25px;
  float: left;
  animation: fadeIn 3s 1.8s backwards;
  white-space: nowrap;

  &:hover {
    // background-color: $primary;
    color: $secondary;
  }
}
