@import '../Sidebar/index.scss';

.contact-page .text-zone {
  top: 20% !important;
}

.contact-form {
  width: 30%;
  position: absolute;
  bottom: 3%;
  left: 10%;

  ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    li {
      padding: 0;
      margin: 0;
      list-style: none;
      margin-bottom: 10px;
      overflow: hidden;
      display: block;
      position: relative;
      opacity: 0;
      animation: fadeInUp 2s 2s;
      animation-fill-mode: forwards;
      align-self: flex-start;
    }

    li.half {
      display: flex;
      width: 100%;

      :first-child {
        margin-right: 1em;
      }
    }

    :nth-child(2),
    :nth-child(3) {
      width: 100%;
    }

  }

  input[type="text"],
  input[type="email"] {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: $light;
    padding: 0px 20px;
    box-sizing: border-box;
  }
  textarea {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: $light;
    padding: 20px;
    box-sizing: border-box;
    min-height: 150px;
  }

  .flat-button {
    color: $primary;
    background: 0 0;
    font-size: 1.6rem;
    letter-spacing: 3px;
    text-decoration: none;
    padding: 10px 12px;
    border: 1px solid $primary;
    float: right;
    border-radius: 4px;
    margin: 0;

    &:hover {
      background-color: $primary;
      color: $light;
      transition: all 1s ease-in-out;
    }
  }
}

.info-map {
  position: absolute;
  background-color: #000;
  top: 50px;
  right: 30%;
  z-index: 999999;
  padding: 20px;
  width: 267px;
  color: $light;
  font-family: 'Helvetica';
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  animation: fadeIn 1s 1.5s;
  animation-fill-mode: forwards;

  span {
    font-size: 16px;
    display: block;
    padding-top: 20px;
    color: $primary;
  }
}

.map-wrap {
  background: rgba(8, 253, 216,0.1);
  // float: right;
  position: fixed;
  right: 0;
  display: block;
  width: 45%;
  top: 0;
  height: 100%;
}

.leaflet-container {
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: backInRight 1s 1.2s;
  animation-fill-mode: forwards;
  margin-block: auto;
}
