// media queries
@import './components/Sidebar/index.scss';
$hamburger-layer-color: $primary;
$hamburger-layer-width : 50px;
$hamburger-layer-height : 6px !default;
$hamburger-padding-x : 20px;
$hamburger-padding-y : 20px;
@import '~hamburgers/_sass/hamburgers/hamburgers.scss';

.hamburger {
  display: none;
}

@media (max-width: 1080px) {
  .page {
    left: calc(10% + 3rem) !important;
  }

  .cubespinner {
    top: 5% !important;
  }

  .cubespinner2 {
    top: 50% !important;
  }

  .cubespinner,
  .cubespinner2 {
    left: 11% !important;
  }

  .home-page .text-zone {
    height: 70% !important;
    width: 70% !important;
  }

  .about-page .text-zone {
    width: 60% !important;
    height: 70% !important;
    top: 45% !important;

    p {
      font-size: 1.5rem !important;
    }
  }

  .face1 {
    transform: translateZ(40px) !important;
  }

  .face2 {
    transform: rotateY(90deg) translateZ(40px) !important;
  }

  .face3 {
    transform: rotateY(90deg) rotateX(90deg) translateZ(40px) !important;
  }

  .face4 {
    transform: rotateY(180deg) rotateZ(90deg) translateZ(40px) !important;
  }

  .face5 {
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(40px) !important;
  }

  .face6 {
    transform: rotateX(-90deg) translateZ(40px) !important;
  }

  .cubespinner,
  .cubespinner2 {
    div {
      width: 80px !important;
      height: 80px !important;
    }
  }

  .map-wrap {
    display: none !important;
  }

  .info-map {
    width: 20rem !important;
    top: 4% !important;
    left: 55%;
  }

  .contact-form {
    width: 50% !important;
    left: 20% !important;
  }
}

@media (max-width: 768px) {
  .nav-bar {
    width: 100% !important;
  }

  .hamburger {
    z-index: 999999;
    display: inline;
    position: absolute !important;
    right: 0 !important;
  }

  .page {
    left: 0% !important;
  }

  .about-page .text-zone {
    width: 80% !important;

    p {
      font-size: 1.4rem !important;
    }
  }

  .cubespinner {
    left: 5% !important;
  }

  .stage-cube-content {
    width: 100% !important;
  }

  .cubespinner2 {
    top: 5% !important;
    left: 30% !important;
  }

  .info-map {
    z-index: -999 !important;
    padding: 14px !important;
    font-size: 14px !important;
    left: 50%;
    width: 18rem !important;

    span {
      font-size: 14px !important;
    }
  }

  .contact-form {
    width: 60% !important;
    left: 20% !important;
  }

  .logo-container {
    top: 18% !important;
    transform: rotate(0deg) !important;
    opacity: 0.6 !important;
  }

  .projects-page {
    left: 0px !important;

    h1 {
      margin-top: 5% !important;
    }
  }

}

@media (max-width: 480px) {
  .tags {
    display: none !important;
  }

  .page {
    overflow: hidden !important;
  }

  .info-map {
    display: none !important;
  }

  .home-page {
    h1 {
      font-size: clamp(12px, 4vh, 30px) !important;

      img {
        width: 40px !important;
        height: 50px !important;
      }
    }

    h2 {
      font-size: clamp(8px, 2vh, 20px) !important;
    }

    .logo-container {
      left: -14%;
      top: 18%;
      transform: rotate(0deg);
      opacity: 0.2 !important;
    }

    .flat-button {
      color: $light !important;
      font-size: 2vh !important;
    }
  }

  .cubespinner {
    left: -2% !important;
  }

  .contact-page {
    h1 {
      font-size: 3.2rem !important;
    }

    .contact-form {
      bottom: 18% !important;
    }
  }

  .projects-page {
    // left: 0px !important;

    h1 {
      font-size: 9vw !important;
    }

    .hover-carousel h1 {
      font-size: 4vw !important;
    }
  }

}

@media (max-width: 320px) {
  // .about-page .text-zone {
  //   p {
  //     font-size: clamp(1vh, 1.25rem, 1.75vh) !important;
  //   }

  //   h1 {
  //     font-size: 3.4rem !important;
  //   }
  // }

  .cubespinner2 {
    top: 60% !important;
    left: 0 !important;
  }

  .stage-cube-content {
    opacity: 0.4 !important;
  }

  .contact-page h1 {
    display: none !important;
  }
}

@media (min-width: 1920px) {
  .logo-container {
    left: 50% !important;

    img {
      width: 80vw !important;
      height: 100vh !important;
    }
  }

  .about-page .text-zone {
    width: 45% !important;

    p {
      font-size: 2.4rem !important;
    }
  }

  .face1 {
    transform: translateZ(110px) !important;
  }

  .face2 {
    transform: rotateY(90deg) translateZ(110px) !important;
  }

  .face3 {
    transform: rotateY(90deg) rotateX(90deg) translateZ(110px) !important;
  }

  .face4 {
    transform: rotateY(180deg) rotateZ(90deg) translateZ(110px) !important;
  }

  .face5 {
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(110px) !important;
  }

  .face6 {
    transform: rotateX(-90deg) translateZ(110px) !important;
  }

  .cubespinner,
  .cubespinner2 {
    div {
      width: 220px !important;
      height: 220px !important;

      img {
        width: 13rem !important;
        height: 13rem !important;
      }
    }
  }

  .contact-page,
  .about-page {
    h1 {
      font-size: 8rem !important;
    }
  }

}

@media (max-width: 240px) {

  body,
  .page {
    overflow-y: scroll !important;
  }

