$primary-color: #ffd700;

@import 'animate.css';
@import '~loaders.css/src/animations/pacman.scss';

@font-face {
  font-family: 'Helvetica';
  font-style: normal;
  font-weight: normal;
  src: url('./fonts/Pragmatica-ExtraLight.woff') format('woff');
}

@font-face {
  font-family: 'La Belle Aurore';
  src: url('./fonts/LaBelleAurore.woff') format('woff');
}

@font-face {
  font-family: 'Coolvetica';
  src: url('./fonts/coolvetica-rg.woff') format('woff');
}

input,
textarea {
  font-family: 'Helvetica';
}


.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  height: 50px;
  width: 50px;
  animation: fadeOut 1s 1s;
  animation-fill-mode: forwards;
}

#root,
.App {
  height: 100%;
}
