@import '../Sidebar/index.scss';

.page {
  width: 100%;
  height: 100%;
  position: relative;
  left: calc(7% + 3rem);
}


.top-tags {
  top: 3%;
}

.tags {
  color: $primary;
  opacity: 0.6;
  position: absolute;
  font-size: 22px;
  font-family: 'La Belle Aurore';
  display: flex;
  flex-direction: column;
  left: 7%;
}

.bottom-tag-html {
  margin-left: -24px;
}

.bottom-tags {
  position: absolute;
  bottom: 1%;
}

.container {
  width: 100%;
  will-change: contents;
  height: 90%;
  min-height: 566px;
  position: relative;
  opacity: 0;
  margin: 0 auto;
  z-index: 1;
  transform-style: preserve-3d;
  animation: fadeIn 1s forwards;
  animation-delay: 1s;

  .text-zone {
    margin-top: 2%;
  }
}

.about-page,
.contact-page {
  .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 45%;
    display: inline;
    vertical-align: middle;
    max-height: 90%;

    h1 {
      font-size: 4.4rem;
      font-family: 'Coolvetica';
      color: $primary;
      font-weight: 400;
      margin-top: 0;
      position: relative;
      margin-bottom: 40px;
      left: 10px;

      &::before {
        content: '<h1>';
        font-family: 'La Belle Aurore';
        font-size: 18px;
        position: absolute;
        margin-top: -10px;
        left: -10px;
        opacity: 0.6;
        line-height: 18px;
      }

      &::after {
        content: '</h1>';
        font-family: 'La Belle Aurore';
        font-size: 18px;
        position: absolute;
        margin-left: 20px;
        left: -30px;
        bottom: -20px;
        opacity: 0.6;
        line-height: 18px;
      }
    }

    p {
      font-size: 1.6rem;
      color: $light;
      font-family: sans-serif;
      font-weight: 500;
      min-width: fit-content;
      animation: pulse 1s;

      &:nth-of-type(1) {
        animation-delay: 1.1s;
      }

      &:nth-of-type(2) {
        animation-delay: 1.2s;
      }

      &:nth-of-type(3) {
        animation-delay: 1.3s;
      }

      &:nth-of-type(4) {
        animation-delay: 1.4s;
      }

      &:nth-of-type(5) {
        animation-delay: 1.5s;
      }
    }
  }

  .text-animate-hover:hover {
    color: $light;
  }
}

.about-page .text-zone {
  width: 55%;
}
